:root {
  --warna-hitam: #000000;
  --warna-putih: #ffffff;
  --warna-abu-abu: #e5e5e5;
}

:root {
  --font-inter: "Inter", sans-serif;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
}

button {
  font-family: var(--font-inter);
}